/** @public */
export type Creates<C> =
  C extends {create(...args: any): infer T} ? T : never;

/** @public */
export interface Creatable<T> {
  create(): T;
}

/** @public */
export const Creatable = {
  [Symbol.hasInstance]<T>(instance: unknown): instance is Creatable<T> {
    if (instance === null || (typeof instance !== "object" && typeof instance !== "function")) {
      return false;
    }
    return typeof (instance as Creatable<T>).create === "function";
  },
};
